*{
    margin: 0%;
    padding: 0%;
}
.st_whole{
   
    margin-top: 5vh;
    background-color:aliceblue;
    display: flex;
    justify-content: space-between;
    padding: 0 15vw;
}
.st_txt{
    display: flex;
    flex-direction: column;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 700;
    width: 50vw;
    justify-content: center;
    gap: 5vh;
    font-size: 1.5vw;
}
.st_desc{
    width:30vw;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
   
  
}
.st_img{
    height: 30vh;
    cursor: pointer;
    border-radius: 5px;
}
.st_img:active{
    transform: scale(1.01);
}