*{
  padding: 0%;
  margin: 0%;
overflow-x: hidden;
}
.whole_app,.whole_side_main{
  position: absolute;
}
.whole_side_main{
  width: 100vw;
  height: 100vh;
  display: flex;
justify-content: center;
align-items: center;
  overflow: hidden;
  background: url('https://cdn.wallpapersafari.com/47/2/ea8LV0.jpg');
}
.whole_side{

  box-shadow: inset 0 0 0 200px rgba(255,255,255,0.05);

  width: 84vw;
  height: 90vh;
padding-top: 5vh;
  
}

.cancel{
  background: url('https://png.pngtree.com/png-vector/20190412/ourlarge/pngtree-vector-cancel-icon-png-image_931207.jpg');
  height: 4vh;
  width: 2vw;

  background-size: cover;
  cursor: pointer;
  margin-left: 2vw;
}
.cancel:active{
  transform: scale(1.1);
}
.cnt{
  display: flex;
  justify-content: center;
  align-items: center;
}
.imgmag{
  height: 70vh;
  width: 60vw;
}