*{
    margin: 0%;
    padding: 0%;
}

.infi_wol{

    display: flex;
    flex-direction: column;
    gap: 8vh;
    margin-top: 20vh;
}
.infi_cvr{
   
    display: flex;
   flex-direction: row;
    padding: 0 2vw;
    height: 30vh;
    

}

.in_card{
  
    height: 27vh;
    margin-right: 2vw;
    width: 20vw;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
.in_card_img{
    height: 25vh;
    width: 20vw;
    border-radius: 5px;
    cursor: pointer;
}
.in_card:hover{
    background-color: rgb(233, 227, 227);
}
.in_card:active{
    transform: scale(1.01);
}
.incard_txt{
    display: flex;
    height: 10vh;
    flex-direction: column;
    align-items: center;
}
