*{
    margin: 0%;
    padding: 0%;
}
.nv_tot{

    background-color: white;
    padding-bottom: 12px;
    display: flex;
    flex-direction: column;
    /* border-bottom: 1px solid black; */
}
.hr_nav{
    padding-top: 3vh;
   border-bottom: 2px solid purple;
   display: flex;
   float: right;
    width: 80vw;

    
}
.hr_scnd_line{
    display: flex;
    justify-content: space-evenly;
    gap: 10vw;
}
.hdr_logo{
    display: flex;
    flex-direction: column;


}
.hdr_img{
  
    width: 6vw;
    height: 10vh;
    background: url('https://media.tenor.com/JDPS5rcIPGIAAAAd/nasa-logo.gif');
    background-size: cover;
    margin-top: 1vh;

}
.hdr_name{
    color: darkslateblue;
    font-weight: 700;
}
.hdr_txt{

    width: 60vw;
    font-family: 'Lobster', cursive;
    font-size: 3vw;
    text-align: right;
    margin-top: 10vh;
    letter-spacing: 0.3vw;
}